<template>
  <div class="container">
    <Header :title="t('index.待成團列表')" type="back"></Header>
    <div class="notformed">
      <div class="lists" v-for="(item,index) in groupInfos" :key="index">
        <div class="li">
          <div class="title flex-item">{{t('index.{key}/已累計{keyb}',{key:item.type === '1'?t('index.超級節點團'):t('index.普通節點團'),keyb:item.invested+'BUSD'})}}</div>
          <div class="btn-wrap" style="margin:0 0 0 10px;" @click="$router.push({name:'Idoa'})">
            <van-button size="mini" type="primary" class="btn" :loading-text="$t('index.我要參團')">{{$t('index.我要參團')}}</van-button>
          </div>
        </div>
        <div class="li">
          <div class="key">{{t('index.隊長地址：')}}</div>
          <div class="value address-copy2" :data-clipboard-text="item.address">
            {{item.address.substring(0,10)}}...{{item.address.substring(item.address.length - 10, item.address.length)}}
            <img src="@/assets/images/copy.png" alt="">
          </div>
        </div>
        <div class="li">
          <div class="key">{{t('index.進度：')}}</div>
          <div class="progress-wrap flex-item">
            <div class="out">
              <div class="in" :style="{'width':item.process}">
                <img class="img" src="@/assets/images/process-btn.png" alt="">
                <!-- <div class="text">{{item.process}}</div> -->
              </div>
            </div>
          </div>
          <div class="text" style="min-width:40px;margin-left:10px;text-align: right;">{{item.process}}</div>
        </div>
      </div>
    </div>

  </div>
</template>
  <script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, getCurrentInstance, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";

import { config } from "@/utils/config";
// import { MaxUint256 } from "@ethersproject/constants";
// import BigNumber from "bignumber.js";
import handleAbi_a from "@/utils/handleAbi_a.json";
import tokenAbi from "@/utils/abi.json";
import { Toast } from "vant";
import BigNumber from 'bignumber.js';
Toast.allowMultiple();
import Clipboard from "clipboard";
export default {
  components: {
    Header
  },
  setup () {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      handleContract: null,
      datagetGroupNumber: '0',
      groupInfos: [],
      usdtContract: null,
      backContract: null,
      idoTypeActions: [],
      initContract: () => {
        data.handleContract = new data.web3.eth.Contract(
          handleAbi_a,
          config.HANDLE_a
        );
        data.backContract = new data.web3.eth.Contract(
          tokenAbi,
          config.backAddress
        );
        data.usdtContract = new data.web3.eth.Contract(
          tokenAbi,
          config.usdtAddress
        );
        data.initData()
      },
      initData: async () => {
        await data.idoTypesFetch(0)
        await data.idoTypesFetch(1)
        data.getGroupNumberFetch()
      },
      getGroupNumberFetch: async () => {
        // 获取未成团数量
        const ToastgetGroupNumber = Toast.loading({
          duration: 0,
        });
        try {
          let res = await data.handleContract.methods.getGroupNumber().call()
          data.datagetGroupNumber = res
          console.log(res, 'getGroupNumberFetch')
          if (Number(res)) {
            data.getGroupInfoFetch()
          }
          ToastgetGroupNumber.clear()
        } catch (error) {
          ToastgetGroupNumber.clear()
          proxy.$errorformat(error)
          console.log(76)
        }
      },
      getGroupInfoFetch: async () => {
        // 获取未成团的团信息
        const ToastgetGroupInfo = Toast.loading({
          duration: 0,
        });
        try {
          let number = data.datagetGroupNumber
          let groupInfos = []
          let decimalsUSDT = await data.usdtContract.methods.decimals().call()
          console.log('decimalsUSDT', decimalsUSDT)
          for (let k = 0; k < number; k++) {
            let res = await data.handleContract.methods.getGroupInfo(k).call()
            console.log(res, 'getGroupInfo')
            let bonuses = res[2]
            bonuses = new BigNumber(bonuses).dividedBy(10 ** decimalsUSDT).toFixed()
            let invested = res[3]
            invested = new BigNumber(invested).dividedBy(10 ** decimalsUSDT).toFixed()
            let type = res[1]
            let process = new BigNumber(invested).dividedBy(data.idoTypeActions[type]._idoUsdtNumber).toFixed()
            process = new BigNumber(process).multipliedBy(100).toFixed()
            groupInfos.push({
              address: res[0],
              type: type,
              bonuses: bonuses,
              invested: invested,
              process: process + '%',
              _idoUsdtNumber: data.idoTypeActions[type]._idoUsdtNumber
            })
          }
          data.groupInfos = groupInfos
          ToastgetGroupInfo.clear()
        } catch (error) {
          ToastgetGroupInfo.clear()
          proxy.$errorformat(error)
          console.log(76)
        }
      },
      idoTypesFetch: async (uint) => {
        const ToastIdoTypes = Toast.loading({ duration: 0 })
        try {
          let _idoTypes = await data.handleContract.methods.idoTypes(uint).call({
            from: data.address
          })
          let decimalsBACK = await data.backContract.methods.decimals().call()
          let decimalsUSDT = await data.usdtContract.methods.decimals().call()
          _idoTypes._idoUsdtNumber = new BigNumber(_idoTypes.idoUsdtNumber).dividedBy(10 ** decimalsUSDT).toFixed()
          _idoTypes._profit = new BigNumber(_idoTypes.profit).dividedBy(10 ** decimalsBACK).toFixed()
          _idoTypes.uint = uint
          if (uint === 0) {
            _idoTypes.name = t('index.普通節點')
          }
          if (uint === 1) {
            _idoTypes.name = t('index.超級節點')
          }
          data.idoTypeActions[uint] = _idoTypes
          console.log('idoTypes:', data.idoTypeActions)
          ToastIdoTypes.clear()
        } catch (error) {
          ToastIdoTypes.clear()
          proxy.$errorformat(error)
          console.log(168)
        }
      },
    })
    onMounted(() => {
      var clipboard = new Clipboard(".address-copy2");
      clipboard.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboard.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
    });
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
  </script>
  <style lang="less" scoped>
.notformed {
  padding: 16px;
  .lists {
    min-height: 105px;
    background: #1a202e;
    border-radius: 8px;
    padding: 12px 12px;
    &:not(:last-child) {
      margin-bottom: 12px;
    }
    .li {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      .title {
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
      }
      .btn-wrap {
        .van-button {
          font-size: 13px;
          font-family: PingFang SC;
          font-weight: bold;
          color: #ffffff;
        }
      }
      .key {
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #ffffff;
      }
      .value {
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #ffffff;
        display: flex;
        align-items: center;

        img {
          width: 16px;
          height: 16px;
          display: inline-block;
          margin-left: 12px;
        }
      }
      .progress-wrap {
        margin-left: 16px;
        .out {
          background: #0e131d;
          border-radius: 5px;
          .in {
            height: 9px;
            background: linear-gradient(0deg, rgba(#2e79fa), rgba(#4fc6e5));
            border-radius: 5px;
            position: relative;
            img {
              position: absolute;
              top: -2.5px;
              right: -7.5px;
              display: block;
              width: 15px;
              height: 15px;
            }
            .text {
              position: absolute;
              top: -2.5px;
              right: -45px;
              font-size: 13px;
              font-family: PingFang SC;
              font-weight: 500;
              color: #ffffff;
              display: inline-block;
            }
          }
        }
      }
    }
  }
}
</style>
<style lang="less">
#app {
  .notformed {
    .btn-wrap {
      .van-button {
        padding: 0 7px;
        font-size: 13px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
      }
    }
  }
}
</style>
  